import { React, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Container } from 'react-bootstrap'
import ItemCard from './ItemCard'
import { getAllItemsAsync } from '../redux/items/thunks'
import { getAllUsersAsync } from '../redux/users/thunks'

// ItemContainer is used in MyItems page
function ItemContainer (props) {
  // selector for userSlice of state
  // state.user.
  const loggedInUser = useSelector(state => state.user)
  console.log('1')
  console.log(loggedInUser)
  const users = useSelector(state => state.allUsers)
  console.log('2')
  console.log(users)
  let userId = users.filter(user => user.username === loggedInUser.username)
  userId = userId[0].id
  // userId = userId.id
  console.log('3')
  console.log(userId)
  const items = useSelector(state => state.items.list)
  const myItems = items.filter(item => item.owner === userId)
  const dispatch = useDispatch()
  console.log('4')
  console.log(myItems)

  useEffect(() => {
    dispatch(getAllUsersAsync())
    dispatch(getAllItemsAsync())
  }, [dispatch])

  return (
    <Container fluid className="item-container">
      {myItems.map(item => {
        return <ItemCard key={item.id}
          id={item.id}
          userId={item.userId}
          image={item.image}
          name={item.name}
          description={item.description}
          type={item.type}
          location={item.location}
          status={item.status}
          delete
          edit
          modalOpen={item.modalOpen}
          changeToReturned
          toggleUnavailable
        />
      })}
    </Container>
  )
}

export default ItemContainer
